import Calendar from './calendar/index'
export default {
  data () {
    return {
      // arr2: ['2018/6/23']
      arr2: []
    }
  },
  components: {
    Calendar
  },
  methods: {
    clickDay (data) {
      console.log('', data)// 
    },
    clickToday (data) {
      console.log('', data)// 
    },
    changeDate (data) {
      console.log('', data)// 
    },
    changeToday () {
      this.$refs.Calendar.ChoseMonth('2018-12-13')// 12121212
    },
    format (date, index) {
      date = new Date(date)
      return `${date.getFullYear()}-${date.getMonth() + 1}-${index}`
    }
  },
  created () {
  }
}



