/**
 * 
 * baseUrl: 
 * routerMode: 
 * imgBaseUrl: 
 */

let baseUrl = ''
let routerMode = 'hash'
let imgBaseUrl = ''
// console.log("sss")
if (process.env.NODE_ENV === 'development') {
  imgBaseUrl = '/img/'
  baseUrl = ''
} else if (process.env.NODE_ENV === 'production') {
  baseUrl = 'http://39.106.188.126:9192'
  imgBaseUrl = 'www.igreenep.cn'
}

export {
  baseUrl,
  routerMode,
  imgBaseUrl
}



