import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'
import actions from './action'
import getters from './getters'

Vue.use(Vuex)

const state = {
  isTure: 'true', // 
  direction: '' ,//   slide-right slide-left slide-reverse(ios)
  positionBulter : 0,
  positionEmer:0,
  positionMon:0,
  userInfo: localStorage.getItem('userInfo'),
  gaodePosition:''
}

export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})



