
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import HeadVue from './head.vue'
export default {
data () {
return {
msg: 'Welcome',
liOne: false,
liOneText: '',
liTwo: false,
liTwoText: ''
}
},
mounted () {

},
components: {
Group,
Cell,
HeadVue
},
methods: {

expand: function (num) {
if (num === 1) {
this.liOne = !this.liOne
if (this.liOne) {
this.liOneText = ''
} else {
this.liOneText = ''
}
} else {
this.liTwo = !this.liTwo
if (this.liTwo) {
this.liTwoText = ''
} else {
this.liTwoText = ''
}
}
}
},
created: function () {


}
}



